import { Component, Element, Event, EventEmitter, h, Host, Method, Prop, Watch } from '@stencil/core';
import Dropzone from 'dropzone';

@Component({
  tag: 'storm-upload',
  styleUrl: 'storm-upload.css',
  shadow: false,
})
export class StormUpload {
  @Element() host: HTMLElement;
  @Prop({ mutable: true }) url: string = '/file/post';
  @Prop() token: string;
  @Event() change: EventEmitter;
  // @Event() complete: EventEmitter;
  @Prop() dataSet: IFile[];
  @Prop({ mutable: true }) imageWidth: number;
  @Prop({ mutable: true }) imageHeight: number;
  @Prop({ mutable: true }) showDetail: boolean = false;
  @Prop({ mutable: true }) showErrorMark: boolean = false;
  @Prop({ mutable: true }) autoProcessQueue: boolean = false;
  @Prop({ mutable: true }) acceptedFiles: string;
  @Prop({ mutable: true }) clickable: boolean = true;
  @Prop({ mutable: true }) droppable: boolean = true;
  @Prop({ mutable: true }) addRemoveLinks: boolean = true;
  @Event() removedFile: EventEmitter<IFile>;
  @Event() addedFile: EventEmitter<IFile>;
  @Event() completeMultiple: EventEmitter<IFile[]>;
  @Event() complete: EventEmitter<IFile>;

  private dropzone: Dropzone;
  private stormModal: HTMLStormModalElement;
  private stormModalImage: HTMLImageElement;
  private dropzoneRef: HTMLElement;
  queueCompleteCallback: Function;

  @Watch('dataSet')
  dataSetWatcher(_newValue: IFile[]) {
    if (this.dropzone) {
      this.dropzone.off('removedfile', this.onRemovedFile);
      this.dropzone.off('addedfile', this.onAddedFile);
      this.dropzone.disable();
      this.dropzone.removeAllFiles();
      this.dropzone.enable();
    }
  }

  @Watch('token')
  tokenWatcher(newValue: string) {
    if (this.dropzone && newValue) {
      this.dropzone.options.headers = { Authorization: 'Bearer ' + newValue };
    }
  }

  @Watch('url')
  urlWatcher(newValue: string) {
    if (this.dropzone && newValue) {
      this.dropzone.options.url = newValue;
    }
  }

  componentWillLoad() {
    this.dataSetWatcher(this.dataSet);
  }

  componentDidRender() {
    if (!this.dropzone) {
      Dropzone.autoDiscover = false; //ç¦æ­¢å°æ‰€æœ‰å…ƒç´ è‡ªå‹•æŸ¥æ‰¾  é˜²æ­¢å ±"Dropzone already attached."çš„éŒ¯èª¤
      this.dropzone = new Dropzone(this.dropzoneRef, {
        url: this.url,
        addRemoveLinks: this.addRemoveLinks,
        dictRemoveFile: 'åˆªé™¤',
        uploadMultiple: true,
        autoProcessQueue: this.autoProcessQueue, //è‡ªå‹•ä¸Šå‚³
        paramName: () => 'file',
        thumbnailWidth: this.imageWidth,
        thumbnailHeight: this.imageHeight,
        acceptedFiles: this.acceptedFiles,
        clickable: this.clickable,
        dictDefaultMessage: '',
      });

      this.dropzone.on('sendingmultiple', this.onSendIngMultiple);

      this.dropzone.on('completemultiple', files => {
        let isSuccess = true;

        files.forEach(f => {
          if (f.status == 'error') {
            isSuccess = false;
            return false;
          }
        });

        if (this.queueCompleteCallback) this.queueCompleteCallback(isSuccess);

        this.completeMultiple.emit(files);
      });

      this.dropzone.on('thumbnail', file => {
        let thumbnail = file.previewElement.firstElementChild;
        thumbnail.onclick = () => {
          this.onClickThumbnail(file.dataURL);
        };

        if (!this.showDetail) thumbnail.nextElementSibling.classList.add('d-none');
        if (!this.showErrorMark) file.previewElement.querySelector('.dz-error-mark').classList.add('d-none');
      });

      // this.dropzone.on('error', function(file, response) {
      //   if (response.detail) {
      //     this.emit('error', file, response.detail);
      //   }
      // });

      this.dropzone.on('complete', file => {
        this.complete.emit(file);
      });

      if (!this.droppable) this.dropzone.on('drop');
    }

    this.dropzone.on('removedfile', this.onRemovedFile);
    this.dropzone.on('addedfile', this.onAddedFile);

    // if(this.imageWidth && this.imageHeight)
    //   this.setWidthHeight()

    if (this.dataSet && this.dataSet.length > 0) this.addFileManually();
  }

  onRemovedFile = file => {
    this.removedFile.emit(file);
  };

  onAddedFile = file => {
    this.addedFile.emit(file);
  };

  onSendIngMultiple = (files: any[], _xhr, formData) => {
    this.host.parentElement.querySelectorAll('[name]').forEach(element => {
      formData.append(element['name'], element['value']);
    });

    files.forEach(f => {
      formData.append('uuid', f.upload.uuid);
    });
  };

  // setWidthHeight() {
  //   document.documentElement.style.setProperty('--image-width', this.imageWidth + "px")
  //   document.documentElement.style.setProperty('--image-height', this.imageHeight + "px")
  // }

  async addFileManually() {
    for (const file of this.dataSet) {
      const uploadItem: IFile = file as IFile;

      if (uploadItem) {
        // this.dropzone.addFile(new File([window.atob(uploadItem.data)], uploadItem.name, {
        //   type: file.type
        // }));

        this.dropzone.addFile(await this.dataURLtoFile(uploadItem.data, uploadItem.name, uploadItem.type));
      }
      // this.dropzone.emit("addedfile", file)
      // this.dropzone.emit("thumbnail", file, file.name)
      // this.dropzone.files.push(file)

      //this.dropzone.addFile(f);
    }
  }

  @Method()
  async addFile(uploadItem: IFile) {
    this.dropzone.addFile(await this.dataURLtoFile(uploadItem.data, uploadItem.name, uploadItem.type));
  }

  async dataURLtoFile(dataUrl, fileName, type) {
    const res: Response = await fetch(dataUrl);
    const blob: Blob = await res.blob();
    return new File([blob], fileName, { type: type });
  }

  onClickThumbnail(dataUrl) {
    this.stormModalImage.setAttribute('src', dataUrl);
    this.stormModal.fire(null);
  }

  @Method()
  async upload(callback: Function) {
    this.queueCompleteCallback = callback;
    this.dropzone.processQueue();
  }

  // @Method()
  // async removeAll(){
  //   this.dataSet = undefined
  //   this.dropzone.removeAllFiles()
  // }

  messageRender() {
    if (this.clickable && this.droppable) {
      return (
        <div class="dz-message">
          <h1 class="svelte-12uhhij">æ‹–æ‹‰æª”æ¡ˆè‡³æ­¤</h1>
          <p>æˆ–é»žæ­¤ä¸Šå‚³</p>
          <p class="comment svelte-12uhhij">å…è¨±åŒæ™‚ä¸Šå‚³å¤šå€‹æª”æ¡ˆ</p>
        </div>
      );
    }
  }

  render() {
    return (
      <Host>
        <div class="dropzone-container">
          <div class="dropzone" ref={e => (this.dropzoneRef = e)}>
            {this.messageRender()}
          </div>
        </div>

        <storm-modal ref={e => (this.stormModal = e)}>
          {/* <swal-html>
              <img alt="" src=""/> ç”¨é€™å€‹ä¼¼ä¹Žä¸æœƒå¢žé•·popupå¤§å°
          </swal-html> */}
          <swal-image src="" alt="" ref={e => (this.stormModalImage = e)} />
          <swal-button type="cancel">é—œé–‰</swal-button>
          <swal-param name="showConfirmButton" value="false" />
        </storm-modal>
      </Host>
    );
  }
}

export interface IFile {
  name: string;
  data: string;
  type: string;
  upload: IUploadFile;
}

export interface IUploadFile {
  uuid: string;
}
